import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import linkState from 'linkstate';
import { route } from 'preact-router';

const initialState = {
    newDog: {},
};

@observer
class Input extends Component {
    render() {
        const { saved, dog, field, title, help, onInput } = this.props;
        const value = dog[field];
        return (
            <div class={`form-group ${saved[`${field}.${dog.id}`] ? 'is-valid' : ''}`}>
                <label for={`${field}Input`}>{title}</label>
                <input type='text' class={`form-control ${saved[`${field}.${dog.id}`] ? 'is-valid' : ''}`} id={`${field}Input`} aria-describedby={`${field}Help`}
                    value={value}
                    data-id={dog.id}
                    data-field={field}
                    onInput={onInput}
                />
                {saved[`${field}.${dog.id}`] ? <div class={`valid-feedback`} style='display: block;'>Endringene er lagret</div> : 
                    <small id={`${field}Help`} class='form-text text-muted'>{help}</small>}
            </div>
        );
    }
}

@observer
class DogsEdit extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
        this.updateTimer = {};
    }

    async loadDog() {
		const { dogStore } = this.props.stores;
        if (this.props.dog === 'new') {
            this.setState({ createNew: true });
        } else {
            dogStore.load(this.props.dog);
        }
    }

	resetDog() {
		this.setState({
            newDog: {},
            createNew: false,
        });
	}

	insertDog = async () => {
		const { newDog } = this.state;
		const { dogStore } = this.props.stores;
		await dogStore.insert({ ...newDog });
        this.resetDog();
        route(`/dogs/`, true);
	}

    updateField = (e) => {
        const { id, field } = e.target.dataset;
        const value = e.target.value;
        const { dogStore } = this.props.stores;

        dogStore.updateField(id, field, value);

        clearTimeout(this.updateTimer[field]);
        this.updateTimer[field] = setTimeout(async () => {
            this.doUpdateField(id, field, value);
        }, 500);
    }

    async doUpdateField(id, field, value) {
        const { dogStore } = this.props.stores;
        await dogStore.saveField(id, field, value);
    }

    back() {
        route('/dogs/', true);
    }

    componentWillMount() {
        this.loadDog();
    }

	render() {
        const { createNew, newDog } = this.state;
		const { dogStore } = this.props.stores;
		const { insertStatus, saved, dog } = dogStore;
        const { name } = dog;
		return (
			<div class='container-fluid' style='margin-bottom: 100px; margin-top: 60px;'>
                <button class='btn btn-link float-left' onClick={this.back}>
                    <i class='fas fa-arrow-left' />
                </button>
				<div class='row'>
					<div class='offset-0 col-12 offset-sm-2 col-sm-8 offset-lg-4 col-lg-4'>
                        {createNew ? (
                            <div> 
                                <h1>Legg til ny hund</h1>
                                <div class='form-group'>
                                    <label for='nameInput'>Navn</label>
                                    <input type='text' class='form-control' id='nameInput' aria-describedby='nameHelp'
                                        onInput={linkState(this, 'newDog.name')}
                                        value={newDog.name}
                                    />
                                    <small id='nameHelp' class='form-text text-muted'>Fullt navn p hunden.</small>
                                </div>
                                <button type='button' class='btn btn-primary'
                                    onClick={this.insertDog}
                                >
                                    Registrer ny hund
                                </button>
                            </div>
                        ) : (
                            <div>
                                <h1>Endre {name}</h1>
                                <Input saved={saved} dog={dog} field='image' title='Bilde' help='Link til bilde av hunden.' onInput={this.updateField} />
                                <Input saved={saved} dog={dog} field='name' title='Fullt navn' help='Fullt navn p hunden.' onInput={this.updateField} />
                                <Input saved={saved} dog={dog} field='shortname' title='Kallenavn' help='Kallenavnet p hunden. Hva roper du p innkalling?' onInput={this.updateField} />
                                <Input saved={saved} dog={dog} field='gender' title='Kjnn' help='Kjnn p hunden. "male", "female"' onInput={this.updateField} />
                                <Input saved={saved} dog={dog} field='chipId' title='Chip ID' help='Chip ID p hunden. Str ofte i vetrinrhndbok eller passet.' onInput={this.updateField} />
                                <Input saved={saved} dog={dog} field='birth' title='Fdselsdato' help='Fdselsdato p hunden. Format p dato er: "YYYY-MM-DD".' onInput={this.updateField} />
                                <Input saved={saved} dog={dog} field='deceased' title='Dd' help='Ddsdato for hunden. Format p dato er: "YYYY-MM-DD".' onInput={this.updateField} />
                            </div>                                                        
                        )}
					</div>

					{insertStatus && <div class='alert alert-success mt-4 col-12' role='alert'>
						Hunden er lagret 
					</div>}
				</div>
			</div>
		);
	}
}

export default DogsEdit;
